<template>
<div class="box">
	<span>Sing</span>
	<input class="search" type="search" placeholder="请输入歌手名称" @keyup.enter="searchSong" v-model="search">
</div>
</template>

<script>
export default {
	name: 'Header',
	data () {
		return {
			search: null,
		}
	},
	methods: {
		searchSong () {
			if(this.search!='' && this.search!=null){
				this.$emit('searchsong',this.search);
			}
		}
	}
}
</script>

<style scoped>
.box{
	width: 100%;
	height: 2.5rem;
	padding: 0 5%;
	color: #fff;
	background: #b11;
	line-height: 2.5rem;
	font-weight: bold;
	font-size: 1.2rem;
	letter-spacing: 0.2em;
}
.search{
	width: 65%;
	height: 1.3rem;
	float: right;
	margin-top: 0.6rem;
	border: none;
	outline: none;
	padding: 0 0.5rem;
	border-radius: 1rem;
	font-size: 0.75rem;
	font-weight: 100;
}
</style>


